.update-02-2020 {
  .programmes {
    position: relative;

    .fa-arrow-right {
      margin-left: 10px;
    }
  }

  .programmes__title {
    text-align: center;
    margin: 0;
    margin-bottom: 70px;
  }

  .programmes__desc {
    text-align: center;
  }

  .programmes__list {
    margin: 0;
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
  }

  .programmes__glide {
    position: relative;
    margin-bottom: 100px;
  }

  .programmes__item {
    height: auto;
  }

  .programmes__slide-wrap {
    margin: 0 auto;
    padding: 14px;
    background-color: #f8f8f8;
    width: 257px;

    &--master {
      margin-top: 1px;
      box-shadow: 0 0 0 1px $red;
    }
  }

  .programmes__status {
    text-transform: uppercase;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  }

  .programmes__status-open {
    margin-right: 5px;
    font-size: 10px;
    line-height: 12px;
    font-weight: 700;
    color: $red;
  }

  .programmes__tag {
    font-size: 10px;
    line-height: 12px;
    font-weight: 700;
    color: $blue;
  }

  .programmes__program-title {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 17px;
    line-height: 23px;
  }

  .programmes__program-desc {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 14px;
    line-height: 16px;
    color: #606060;
  }

  .programmes__info {
    font-size: 12px;
    line-height: 14px;
    color: #262626;
    margin-bottom: 25px;
  }

  .programmes__info-desc {
    margin: 0;
    padding: 0;
    margin-bottom: 7px;
    color: #6b6b6b;
  }

  .programmes__info-name {
    font-weight: 700px;
    color: #262626;
  }

  .programmes__more {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: .155em;
    color: $green;
    text-transform: uppercase;
  }

  .programmes__arrow-wrap {
    position: absolute;
    width: 100%;
    top: 42%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .programmes__arrow {
    position: absolute;
    top: 0;
    display: block;
    width: 15px;
    height: 15px;
    padding: 0;
    margin: 0;
    background-color: transparent;
    border: solid 2px #303030;
    border-left: none;
    border-top: none;

    &--left {
      left: 0;
      transform: rotate(135deg);
    }

    &--right {
      right: 0;
      transform: rotate(-45deg);
    }
  }

  .programmes__programm-long {
    .programmes__item {
      display: block;
    }
  }

  .programmes__title-long {
    font-size: 26px;
    line-height: 34px;
    text-align: center;
    margin-top: 0;
    margin-bottom: 40px;
  }

  .programmes__desc-long {
    margin-top: 0;
    margin-bottom: 70px;
    text-align: center;
  }

  .programmes__page-master {
    background-color: #f4f4f4;
    padding-top: 50px;
    padding-right: 22px;
    padding-bottom: 50px;
    padding-left: 22px;
    box-shadow: 0 0 0 1px $red;
  }

  .programmes__item {
    display: flex;
    margin-top: 1px;
    margin-bottom: 1px;
  }

  .programmes__item-wrap {
    padding-top: 50px;
    padding-left: 18px;
    padding-right: 18px;

    .programmes__status {
      max-width: 230px;
    }
  }

  .programmes__program-title-long {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 22px;
    line-height: 29px;
    font-weight: 400;
  }

  .programmes__program-desc-long {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 16px;
    line-height: 19px;
  }
}


@media(min-width: 660px) {
  .update-02-2020 {
    .programmes__glide {
      width: 100%;
    }
  }
}

@media(min-width: $tablet-width) {
  .update-02-2020 {
    .programmes {
      width: 100%;
    }

    .programmes__title {
      text-align: left;
      margin-bottom: 30px;
    }

    .programmes__glide {
      margin-bottom: 75px;
    }

    .programmes__list {
      flex-wrap: wrap;
      overflow: visible;
    }

    .programmes_glide-track {
      overflow: visible;
    }

    .programmes__item {
      justify-content: flex-start;
      margin-right: 25px;
      width: 257px;
      margin-bottom: 25px;

      &:nth-child(3n+3) {
        margin-right: 0;
      }
    }

    .programmes__programm-long {
      .programmes__page-master {
        display: flex;
      }

      .programmes__program-addition {
        display: flex;
        flex-direction: column;
      }

      .program-addition__list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
      }

      .programmes__item {
        width: auto;
        margin-right: 0;
      }
    }

    .programmes__slide-wrap {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      flex-wrap: wrap;
      max-width: none;
      margin: 0;
    }

    .programmes__arrow-wrap {
      display: none;
    }

    .programmes__title-long {
      margin-bottom: 20px;
    }

    .programmes__desc-long {
      margin-bottom: 30px;
    }

    .programmes__title-long,
    .programmes__desc-long {
      text-align: left;
    }

    .programmes__item-wrap {
      padding-top: 0;
      padding-left: 35px;
    }

    .programmes__page-master {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
}

@media(min-width: $desktop-width) {
  .update-02-2020 {
    .programmes__item {
      width: 255px;
      margin-right: 25px;
      flex-basis: auto;

      &:nth-child(3n+3) {
        margin-right: 25px;
      }

      &:nth-child(4n+4) {
        margin-right: 0;
      }
    }
  }
}