.update-02-2020 {
  .message {
    max-width: 1100px;
    margin: 0 auto;
    background-color: #eeeeee;
    text-align: center;
    padding-top: 60px;
    padding-bottom: 60px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .message__desc {
    max-width: 265px;
    margin: 0 auto;
    margin-bottom: 35px;
    padding: 0;
    font-family: $font-accent;
    color: #383838;
  }

  .message__btn {
    @include btn;
    max-width: 265px;
    margin: 0 auto;
    background-color: $red;
  }
}

@media(min-width:$tablet-width) {
  .update-02-2020 {
    .message {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: 20px;
      padding-bottom: 22px;
    }

    .message__desc {
      max-width: 70%;
      margin: 0;
      margin-right: 10px;
      text-align: left;
    }

    .message__btn {
      margin-right: 0;
    }
  }
}